import React from "react";

import "./Trayectoria.css";
import Profesor from "./img/profilepic.jpg";
import hawc from "./img/Hawc.jpg";

function Trayectoria() {
  return (
    <div>
      <div className="trayectoria">
        <section class="imagenTrayectoria">
          <img
            className="imagenTrayectoriaHawc"
            src={hawc}
            alt="Imagen de la institucin"
          />
          <div className="nombre">Dr. Jess Alberto Martnez Castro</div>
        </section>
        <div className="nombreOculto">Dr. Jess Alberto Martnez Castro</div>
        <br />
        <br />
        <br />
        <div className="informacion">
          <div className="contenedorImagenTrayectoria">
            <img src={Profesor} className="imagenProfesor" alt="" />
          </div>
          <div className="contenedorInfo">
            Miembro del Sistema Nacional de Investigadores (I), actualmente
            colabora con el experimento HAWC (High Altitude Water Cherenkov; con
            sede en la Sierra Negra, en los lmites entre Veracruz y Puebla.
            <br />
            <br />
            Jess, ha sido profesor del Instituto Politcnico Nacional desde
            Septiembre de 1995 a la fecha y se encuentra adscrito al Centro de
            Investigacin en Computacin como Profesor Titular C (Categora
            Mxima que otorga el IPN). <br />
            <br />
            Jess, tambin ha sido evaluador de artculos en diversas revistas
            adscritas al Journal of Citation Report. Su especialidad es Fsica
            Nuclear y Partculas elementales de altas energas aunque ha
            colaborado tambin en proyectos interdisciplinarios
            (Fsica-Medicina-Cmputo) y en proyectos de computacin paralela y
            distribuida (Actualmente colaborando con el desarrollo de la GRID,
            la cual es considerada como la siguiente generacin de Internet).
          </div>
        </div>

        <a
          className="contenedorInfo perfil"
          href="https://ipn.elsevierpure.com/es/persons/jes%C3%BAs-alberto-mart%C3%ADnez-castro"
        >
          {" "}
          Perfil{" "}
        </a>
        <div className="proyectos"></div>
      </div>
      {/*<InicioAlumno></InicioAlumno>
      <Alumno></Alumno>
  <Alumno></Alumno>*/}
    </div>
  );
}

export default Trayectoria;
